<template>
  <div>
    <div id="staff_mag"> 
      <!-- 导航 -->
      <div class="nav">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item >提现审核</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="searchGroup">
          <el-row :gutter="10">
            <el-col :span="5">
              <el-select v-model="search_shzt" placeholder="类型">
                <el-option label="全部" value="00">全部</el-option>
                <el-option
                  v-for="item in ztList"
                  :key="item.DM"
                  :label="item.DMMC"
                  :value="item.DM">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="9">
              <el-date-picker
                size="medium"
                v-model="search_sqrq"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="录入时间起"
                end-placeholder="录入时间止"
                format="yyyy 年 MM 月 dd 日"
                value-format="yyyy-MM-dd"
                :picker-options="pickerOptions"
              ></el-date-picker>
            </el-col>
            <el-col :span="7">
              <div class="grid-content bg-purple">
                <el-input v-model="search_bz" placeholder="请输入备注搜索"></el-input>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple search-button">
                <el-button type="primary" @click="searchFun">&nbsp;搜索&nbsp;</el-button>
              </div>
             </el-col> 
          </el-row>
        </div>
      </div>

      <!-- 表格区域 -->
      <div class="table_container">
        <div class="muti_container">

          <el-table v-loading="loading" :data="list" height="100%" border  style="width:100%"
            ref="singleTable">
            <el-table-column prop="XM" label="用户名"></el-table-column>
            <el-table-column prop="PHONE" label="手机号"></el-table-column>
            <el-table-column prop="TX_JE" label="提现金额"></el-table-column>
            <el-table-column prop="ZT" label="审核状态"></el-table-column>
            <el-table-column prop="BZ" label="备注"></el-table-column>
            <el-table-column prop="YXBZ" label="有效标志"></el-table-column>
            <el-table-column prop="LRSJ" label="录入时间" width="155"></el-table-column>
            <el-table-column label="操作" width="100" align="center">
                <template slot-scope="props">
                  <el-button type="success" size="mini" style="margin:0 auto;" @click.native="audit(props.row)" round>审核</el-button>
                </template>
            </el-table-column>
          </el-table>
          <div class="table_info_bar">
            <div class="block">
              <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="10" layout="total, prev, pager, next, jumper" :total="totalSize">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>

     <!-- 审核 -->
      <el-dialog title="审核" :visible.sync="isShowDialog" width="50%" :close-on-click-modal="false">
        <div style="margin: -14px 5px;">          
          <el-form >
            `<el-row>                
                <el-col :span="24">
                  <div class="grid-content">
                    <el-form-item label="提现申请状态" :label-width="formLabelWidth">
                       <el-select v-model="zt" placeholder="请选择提现申请状态">
                        <el-option v-for="item in ztList" :key="item.DM" :label="item.DMMC" :value="item.DM"></el-option>
                      </el-select>
                    </el-form-item>
                  </div>
                </el-col>
              </el-row> 
              <el-row>                
                <el-col :span="24">
                  <div class="grid-content">
                    <el-form-item label="备注" :label-width="formLabelWidth">
                      <el-input type="textarea" v-model="bz"  placeholder="请输入备注"></el-input>
                    </el-form-item>
                  </div>
                </el-col>
              </el-row>  
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="auditForm">确定</el-button>
          </div>
        </div>
      </el-dialog>

   
  </div>
</template>

<style lang="scss" scoped>
  @import './withdrawal_audit.scss';
</style>

<script>
  import Axios from 'axios'
  var menu_uuid="E3335SFDG0C84353223339C05D23S20W";
  export default {
    data() {    
      return {
        loading:false,
        isShowDialog:false,
        formLabelWidth:'100px',
        search_bz:'',
        search_shzt:'',
        list:[],
        totalSize:0,
        currentPage:1,
        selectList:[],
        bz:'',
        zt:'',
        ztList:'',
        search_sqrq:'',
        pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      }
    },
    methods: {
      //搜索
      searchFun(){
        this.queryManualAccount(this.search_shzt,this.search_sqrq,this.search_bz,1);
      },
      // 表格换页处理
      handleCurrentChange(val){
        this.queryManualAccount(this.search_shzt,this.search_sqrq,this.search_bz,val);
      },
      audit(row){
        this.selectList=row;
        console.log(this.selectList);
        this.isShowDialog=true;
        this.bz=this.selectList.BZ;
        this.zt=this.selectList.TX_ZT;
      },
      auditForm(){
        if(this.bz=="" || this.bz==null){
          this.$message({
            type: 'error',
            message: '请输入备注'
          });
          return false;
        }
        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:"updateTxsq", 
            txUuid:this.selectList.TX_UUID,
            txzt:this.zt,
            bz:encodeURI(this.bz)
          }
          }).then(function (response) {
            console.log(response.data);
          if(response.data.result=="Fail"){
            that.$message({
              type: 'error',
              message: response.data.msg
            });
          }else{
            that.$message({
              type: 'success',
              message: response.data.msg
            });            
            that.isShowDialog=false;
            that.queryManualAccount(that.search_shzt,that.search_sqrq,that.search_bz,1);
          }
        })
        .catch(function (error) {
          
        }) 
      },
      cancel(){
        this.isShowDialog=false;
      },
      // 表格数据初始化
      queryManualAccount(_shzt,_sqrq,_bz,_start){
        this.loading=true;
        let qs = '';
        let qz = '';
        if (_sqrq) {
          qs = _sqrq[0];
          qz = _sqrq[1];
        }
        if(_shzt=="00"){
          _shzt="";
        }
        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:'queryTxsqList', 
            shzt:_shzt,
            bz:encodeURI(_bz),
            lrsjq:qs,
            lrsjz:qz,
            start:_start

          }
          }).then(function (response) {
            console.log(response.data)
            if(response.data.result=="Fail"){ 
              that.list=[];
              that.totalSize=0;
            }else{
              that.list=response.data.list;
              that.totalSize=response.data.list[0].ZS; 
            }
           that.loading=false;
        })
        .catch(function (error) {
          that.loading=false;
        }) 
      },
       // 初始化查询类型
      initDate() {
        var that = this;
        Axios({
            methods:'post',
            url:this.projectName+'CommonServlet',
            params:{
            action:"queryDmb",
            dmType:encodeURI('提现申请状态')
            }
            }).then(function (response) {
            console.log(response.data);
            that.ztList = response.data.dmList;     
        })
        .catch(function (error) {
            
        })
      }
      
    },
    created:function(){
      
    },
    watch: {
    },
    mounted:function(){
        this.queryManualAccount(this.search_shzt,this.search_sqrq,this.search_bz,1);     
        this.initDate();  
    }
  };
</script>


